<script>
export default {
  extends: require("vuetify/lib").VAppBar, //   Vuetify   . template   .
  data() {
    return {};
  },
  computed: {
    computedTransform() {
      return this.isActive ? 0 : -80;
    },
    computedContentHeight() {
      return 56;
    },
  },
  methods: {},
  props: {},
  watch: {},
  created() {
    document.addEventListener("mousemove", (event) => {
      if (event.clientY <= 10) {
        this.isActive = true; //     
      }
    });
  },
  mounted() {},
  destroyed() {},
};
</script>
<style scoped>
.v-toolbar__content {
  padding: 4px;
}
</style>